import React, {type MouseEventHandler, useState} from 'react'
import SuperButton from '../hw04/common/c2-SuperButton/SuperButton'
import {restoreState} from '../hw06/localStorage/localStorage'
import s from './Clock.module.css'

function Clock() {
    const [timerId, setTimerId] = useState<number | undefined>(undefined)
    // for autotests //   //      ,     
    const [date, setDate] = useState<Date>(new Date(restoreState('hw9-date', Date.now())))
    const [show, setShow] = useState<boolean>(false)

    const start = (event: React.MouseEvent<HTMLButtonElement>) => {
        //   //   (   ,   +1)
        //    (https://learn.javascript.ru/settimeout-setinterval#setinterval)
        const timer = setInterval(() => setDate(new Date), 1000)
        setTimerId(Number(timer))
    }

    const stop = () => {
        //   //    ,    (timerId <- undefined)
        clearInterval(timerId)
        setTimerId(undefined)
    }

    const onMouseEnter = () => { //   //     
        setShow(true)
    }
    const onMouseLeave = () => { //   //      
        setShow(false)
    }

    const timeBlock = {
        hour: 'numeric',
        minute: 'numeric',
        second: 'numeric',
    } as const

    const formatterTimeBlock = new Intl.DateTimeFormat('ru', timeBlock)
    const formattedTimeBlock = formatterTimeBlock.format(date)

    const day = new Intl.DateTimeFormat('en', { day: '2-digit' }).format(date); // "15"
    const month = new Intl.DateTimeFormat('en', { month: '2-digit' }).format(date); // ""
    const monthString = new Intl.DateTimeFormat('en', { month: 'long' }).format(date);
    const year = new Intl.DateTimeFormat('en', { year: 'numeric' }).format(date); // "2025"
    const weekday = new Intl.DateTimeFormat('en', { weekday: 'long' }).format(date); // ""


    const stringTime = formattedTimeBlock || <br/> // 24:: (01:02:03)/(23:02:03)/(24:00:00)/(00:00:01) //  
    const stringDate = `${day}.${month}.${year}` || <br/> // .. (01.02.2022) //  ,  01.02.0123/01.02.-123/01.02.12345  

    //    ,    (https://learn.javascript.ru/intl#intl-datetimeformat)
    const stringDay = weekday || <br/> //  
    const stringMonth = monthString || <br/> //  

    return (
        <div className={s.clock}>
            <div
                id={'hw9-watch'}
                className={s.watch}
                onMouseEnter={onMouseEnter}
                onMouseLeave={onMouseLeave}
            >
                <span id={'hw9-day'}>{stringDay}</span>,{' '}
                <span id={'hw9-time'}>
                    <strong>{stringTime}</strong>
                </span>
            </div>

            <div id={'hw9-more'}>
                <div className={s.more}>
                    {show ? (
                        <>
                            <span id={'hw9-month'}>{stringMonth}</span>,{' '}
                            <span id={'hw9-date'}>{stringDate}</span>
                        </>
                    ) : (
                        <>
                            <br/>
                        </>
                    )}
                </div>
            </div>

            <div className={s.buttonsContainer}>
                <SuperButton
                    id={'hw9-button-start'}
                    disabled={timerId !== undefined} //   //    
                    onClick={start}
                >
                    start
                </SuperButton>
                <SuperButton
                    id={'hw9-button-stop'}
                    disabled={timerId === undefined} //   //     
                    onClick={stop}
                >
                    stop
                </SuperButton>
            </div>
        </div>
    )
}

export default Clock
